$boxSize: 40px;

* {
  box-sizing: border-box;
  font-family:  "Gill Sans Extrabold", sans-serif;
}

#app {
  max-width: calc(#{$boxSize} * 8);
  max-height: calc(#{$boxSize} * 8);

  display: flex;
  flex-wrap: wrap;


  .box {
    width: $boxSize;
    height: $boxSize;
    background-color: greenyellow;
    border: 1px solid rgba(169, 122, 30, 0.964);
    display: flex;
    align-items: center;
    justify-content: center;

    &.initial {
      border: 1px solid rgb(104, 89, 62);
      border-style: outset;
      cursor: pointer;
    }

    &.bomb-count-1 {
      color: blue;
     
    }

    &.bomb-count-2 {
      color: rgb(32, 82, 32);
      
    }

    &.bomb-count-3 {
      color: rgb(114, 41, 41);
      
      
    }

    &.bomb-count-4 {
      color: rgb(0, 0, 255);
    
      
    }

    &.bomb-count-5 {
      color: rgb(99, 19, 19);
      

    }

    &.bomb-count-6 {
      color: black;
     
    }

    &.bomb-count-7 {
      color: black;
     
    }

    &.bomb-count-8 {
      color: white;
      
    }
  }
}
